import styles from './OrderList.module.scss';
import OrderCard from './components/OrderCard/OrderCard';

const OrderList = ({ order, onCancelClick }) => {
  const orders = [
    {
      image: 'https://avatars.mds.yandex.net/get-mpic/1361544/img_id3625151140723044197.jpeg/orig',
      text: '    , ',
      tag: ' ',
      counter: 3,
      number: 1231234,
    },
    {
      image: 'https://avatars.mds.yandex.net/get-mpic/1361544/img_id3625151140723044197.jpeg/orig',
      text: '.   .  " - 2",  " 2".   . 270 .',
      tag: [[''], [' ']],
      counter: 2,
      number: 1231234,
    },
    {
      image: 'https://avatars.mds.yandex.net/get-mpic/1361544/img_id3625151140723044197.jpeg/orig',
      text: '  ,     ,   , 48   ',
      tag: [' '],
      counter: 1,
      number: 1231234,
    },
  ];

  return (
    <>
      <ul>
        {orders?.map((order, i) => (
          <OrderCard
            key={i}
            image={order.image}
            text={order.text}
            tags={order.tag}
            counter={order.counter}
            number={order.number}
            onCancelClick={onCancelClick}
            isExpanded={false}
          />
        ))}
      </ul>
    </>
  );
};

export default OrderList;
